"use client"

import { Component, ReactNode } from 'react'
import { Button } from '@/components/ui/button'
import { AlertCircle } from 'lucide-react'

interface Props {
  children: ReactNode
  fallback?: ReactNode
}

interface State {
  hasError: boolean
  error: Error | null
  errorCount: number
}

export class ErrorBoundary extends Component<Props, State> {
  private resetTimeout: NodeJS.Timeout | null = null

  constructor(props: Props) {
    super(props)
    this.state = { hasError: false, error: null, errorCount: 0 }
  }

  static getDerivedStateFromError(error: Error): Partial<State> {
    // Prevent infinite loops by limiting error count
    return { hasError: true, error }
  }

  componentDidCatch(error: Error, errorInfo: React.ErrorInfo) {
    // Log error to console in development
    if (process.env.NODE_ENV === 'development') {
      console.error('Error caught by boundary:', error, errorInfo)
    }
    
    // Increment error count to prevent infinite loops
    this.setState((prevState) => ({
      errorCount: prevState.errorCount + 1
    }))

    // If we've caught too many errors, force a page reload
    if (this.state.errorCount >= 3) {
      console.error('Too many errors, forcing page reload')
      setTimeout(() => {
        window.location.reload()
      }, 1000)
    }
    
    // In production, you would send this to an error tracking service
    // e.g., Sentry, LogRocket, etc.
  }

  componentDidUpdate(prevProps: Props, prevState: State) {
    // Reset error state if children change (new route/page)
    if (prevProps.children !== this.props.children && this.state.hasError) {
      this.setState({ hasError: false, error: null, errorCount: 0 })
    }
  }

  componentWillUnmount() {
    if (this.resetTimeout) {
      clearTimeout(this.resetTimeout)
    }
  }

  handleReset = () => {
    // Clear any pending timeouts
    if (this.resetTimeout) {
      clearTimeout(this.resetTimeout)
    }
    
    // Reset state
    this.setState({ hasError: false, error: null, errorCount: 0 })
  }

  render() {
    // If we've had too many errors, show a simple message
    if (this.state.errorCount >= 3) {
      return (
        <div className="flex min-h-screen items-center justify-center bg-gray-50 p-4">
          <div className="w-full max-w-md rounded-lg bg-white p-6 shadow-lg text-center">
            <AlertCircle className="h-12 w-12 text-red-600 mx-auto mb-4" />
            <h2 className="text-xl font-semibold text-gray-900 mb-4">
              Multiple errors detected
            </h2>
            <p className="text-gray-600 mb-6">
              The page will reload automatically...
            </p>
          </div>
        </div>
      )
    }

    if (this.state.hasError) {
      if (this.props.fallback) {
        return this.props.fallback
      }

      try {
        return (
          <div className="flex min-h-screen items-center justify-center bg-gray-50 p-4">
            <div className="w-full max-w-md rounded-lg bg-white p-6 shadow-lg">
              <div className="flex items-center gap-3 mb-4">
                <AlertCircle className="h-6 w-6 text-red-600" />
                <h2 className="text-xl font-semibold text-gray-900">
                  Something went wrong
                </h2>
              </div>
              
              <p className="text-gray-600 mb-6">
                An unexpected error occurred. Please try refreshing the page or contact support if the problem persists.
              </p>

              {process.env.NODE_ENV === 'development' && this.state.error && (
                <div className="mb-6 rounded bg-gray-100 p-4">
                  <p className="text-sm font-mono text-gray-800 break-words">
                    {this.state.error.message}
                  </p>
                </div>
              )}

              <div className="flex gap-3">
                <Button
                  onClick={this.handleReset}
                  className="flex-1"
                >
                  Try Again
                </Button>
                <Button
                  onClick={() => window.location.reload()}
                  variant="outline"
                  className="flex-1"
                >
                  Refresh Page
                </Button>
              </div>
            </div>
          </div>
        )
      } catch (renderError) {
        // If rendering the error UI itself causes an error, show a minimal fallback
        console.error('Error rendering error boundary UI:', renderError)
        return (
          <div className="flex min-h-screen items-center justify-center bg-gray-50 p-4">
            <div className="text-center">
              <h2 className="text-xl font-semibold text-gray-900 mb-4">
                An error occurred
              </h2>
              <Button onClick={() => window.location.reload()}>
                Refresh Page
              </Button>
            </div>
          </div>
        )
      }
    }

    return this.props.children
  }
}

